
import { createComponent } from '@vue/composition-api'

export default createComponent({
  props: {
    value: {
      type: Number,
      required: true,
    },
  } as const,

  setup(props, { emit }) {
    const down = () => {
      if (props.value <= 1) {
        return
      }

      emit('input', props.value - 1)
    }

    const up = () => emit('input', props.value + 1)

    return { down, up }
  }
})
